<template>
  <div class="row">
    <div class="col-xs-12 col-sm-4 col-md-3">
      <div class="panel panel-default" style="min-height: 300px">
        <div class="panel-heading">
          <h3 class="panel-title">
            <span @click="goBack" v-if="currentCategoryName" class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
            {{ currentCategoryName || 'Categories' }}
          </h3>
        </div>

        <ul class="list-group" style="overflow-x: hidden;">
          <transition appear name="slide-fade" mode="out-in"
            :enter-class="isForwardDirection ?  'next-slide-fade-enter' : 'back-slide-fade-enter'"
            :leave-active-class="isForwardDirection ? 'next-slide-fade-leave-active' : 'back-slide-fade-leave-active'">

            <div :key="currentVisibleList">

              <template v-for="(category, index) in currentVisibleList">
                <a :key="category.categoryPath" class="list-group-item" style='color: black'
                  v-if="category.children" @click="changeRootCategory( index )" >{{ category.categoryName }}
                  <span class="badge">{{ category.children.length }}</span></a>
                <a :key="category.categoryPath" class="list-group-item" style="color: gray" v-else > {{ category.categoryName }} </a>
              </template>

            </div>

          </transition>
        </ul>

      </div>
    </div>
  </div>
</template>

<script>
const treeData = require('../lib/fake_data')

export default {

  name: 'app',
  data () {
    return {
      currentVisibleList: treeData, // shows current item on the current category
      history: [], // track parent category; essential to make goBack() method work
      isForwardDirection: true // animatino
    }
  },

  computed: {
    categories: function () {
      return treeData
    },
    currentCategoryName: function () {
      if (this.history.length >= 1) {
        return this.history[ this.history.length - 1 ].name
      }
    }
  },

  methods: {
    changeRootCategory: function (_index) {
      const subCategory = this.currentVisibleList[_index]
      this.currentVisibleList = subCategory.children

      this.history.push({
        index: _index,
        name: subCategory.categoryName
      })

      this.isForwardDirection = true
    },

    goBack: function () {
       // update history array
      this.history.pop()

      // go to root category
      if (this.history.length <= 0) {
        this.currentVisibleList = this.categories
        return
      }

      // we need to loop through history to find parent list
      let tempArray = this.categories

      for (let item of this.history) {
        tempArray = tempArray[ item.index ].children
      }

      this.currentVisibleList = tempArray

      this.isForwardDirection = false
    }
  }
}
</script>

<style type="sass">
/********************* Animation Style ************************************/
/* Enter and leave animations can use different */
/* durations and timing functions.              */
.next-slide-fade-enter{
  transform: translateX(20px);
  opacity: 0;
}

.slide-fade-enter-active {
  transition: all .5s ease;
}

.next-slide-fade-leave-active {
  opacity: 0;
  transition: all 0.2s ease;
  transform: translateX(-20px);
}

.back-slide-fade-enter{
  transform: translateX(-20px);
  opacity: 0;
}

.back-slide-fade-leave-active {
  opacity: 0;
  transition: all 0.2s ease;
  transform: translateX(20px);
}


/********************* PANEL STYLE ************************************/
body
{
    padding-top: 20px;
    background-color: #F7F7F7;
}
.panel > .list-group
{
    margin-bottom: 0;
}
.panel > .list-group .list-group-item
{
    border-width: 1px 0;
}
.panel > .list-group .list-group-item:first-child
{
    border-top-right-radius: 0;
    border-top-left-radius: 0;
}
.panel > .list-group .list-group-item:last-child
{
    border-bottom: 0;
}
.panel-heading + .list-group .list-group-item:first-child
{
    border-top-width: 0;
}
.panel-default .list-group-item.active
{
    color: #000;
    background-color: #DDD;
    border-color: #DDD;
}
.panel-primary .list-group-item.active
{
    color: #FFF;
    background-color: #428BCA;
    border-color: #428BCA;
}
.panel-success .list-group-item.active
{
    color: #3C763D;
    background-color: #DFF0D8;
    border-color: #D6E9C6;
}
.panel-info .list-group-item.active
{
    color: #31708F;
    background-color: #D9EDF7;
    border-color: #BCE8F1;
}
.panel-warning .list-group-item.active
{
    color: #8A6D3B;
    background-color: #FCF8E3;
    border-color: #FAEBCC;
}
.panel-danger .list-group-item.active
{
    color: #A94442;
    background-color: #F2DEDE;
    border-color: #EBCCD1;
}
.panel a.list-group-item.active:hover, a.list-group-item.active:focus
{
    color: #000;
    background-color: #DDD;
    border-color: #DDD;
}

</style>



